import React, { useState } from 'react';
import data from './data';
import SingleQuestion from './Question';

function App() {
  const [questions, setQuestions] = useState(data);


  return <main>
    <div class='container'>
      <section>
        <h2>Q&amp;A</h2>
        {questions.map((question) => {
          return <SingleQuestion key={question.id} {...question} />
        })}
      </section>
    </div>
  </main>;
}

export default App;
